import Vue from 'vue'
import Router from 'vue-router'
import moment from 'moment'

// 承载
import verifyApplyInfo from '../pages/verifyApplyInfo.vue'
// 主页
import verifyApplyInfoIndex from '../views/verify-apply-info.vue'

Vue.use(Router)

Vue.filter('dateFormat', function (value, type) { //时间格式化过滤
  return moment(value).format(type);
});

const router = new Router({
  routes: [{
      path: '/',
      name: 'verifyApplyInfo',
      component: verifyApplyInfoIndex
    }
  ]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#verifyApplyInfo',
  router,
  components: {
    verifyApplyInfo
  },
  template: '<verifyApplyInfo/>'
})



